.main-header__wrapper {
  position: relative;
  width: @mobile-width;
  min-height: 79px;
  padding: 0 30px;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  box-shadow:
    0 1px 0 @color-light-grey,
    inset 0 1px 0 @color-light-grey;
}

.main-header__logo {
  margin: auto;
}

.main-header__delivery-info {
  display: none;
}

@media (min-width: @tablet-width) {
  .main-header__wrapper {
    width: @tablet-width;
    min-height: 89px;
    padding: 0 50px;
    justify-content: space-between;
    flex-direction: row;
  }

  .main-header__logo {
    margin-left: 0;
  }

  .main-header__delivery-info {
    position: absolute;
    margin: 0;
    right: 50px;
    bottom: -44px;
    display: block;
  }
}

@media (min-width: @desktop-width) {
  .main-header__wrapper {
    width: @desktop-width;
  }

  .main-header__logo {
    position: absolute;
    left: 50%;
    top: 22px;
    transform: translateX(-50%);
  }
}
